import Vue from 'vue'
import Router from 'vue-router'
import Index from '@/views/index.vue'
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Index',
      component: Index
    },
    // {
    //   path: '/',
    //   name: 'Hello',
    //   component: Hello
    // }
  ]
})



